body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

#welcome {
  text-align: center;
  color: white;
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSvtP-h0nsQMHLOg_LPyrA7TpNgnoSIrWdGnA&usqp=CAU");
}

#login{
  -webkit-transform: translateY(10rem);
          transform: translateY(10rem);
  font-size: 25px;}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  margin-left: 10px;
}

.container {
  -webkit-transform: translateX(12.5rem);
          transform: translateX(12.5rem);
  height: 750px;
  position: absolute;
  top: 80%;

}
.part {
  width: 350px;
  height: 350px;
  margin-left: 0.25em;
  margin-right: 0.25em;
  margin-bottom: 1.0em;
  display: inline-block;
  background: rgb(227, 229, 233);
  text-align: center;
}
#navigation{
  width:100%;
}

#number {
    position: relative;
    top: 5%;
    left: 5%;
    width: 10%;
    height: 10%;
    -webkit-transform: rotate(-45deg);
    background: rgb(115, 90, 202);
  }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

